<template>
  <div class="good-class-picture">
    <ul class="spu-list">
      <li v-for="(item,index) of pictureList" :key="item.imgSrc" class="spu-item" :class="{selected:selectPictureList.includes(item.imgSrc)}" @click="selectPicture(item.imgSrc, index)">
        <div class="img-wrapper">
          <img :src="item.imgSrc" alt="" crossorigin>
        </div>
        <div class="name">{{item.skuShortName}}</div>
      </li>
    </ul>
    <div class="picture-content" :class="{unvisitable:pictureList.length===0}">
      <div class="picture-list">
        <div v-show="topPictureUrl" class="top-line">
          <img :src="topPictureUrl" alt="" crossorigin>
        </div>
        <div class="other-picture-list">
          <img v-for="item of otherPictureUrlList" :key="item" :src="item" alt="" crossorigin>
        </div>
      </div>
      <div class="good-info">
        <div class="title-line">
          <div class="name">{{mainProductName}}</div>
          <div class="price">
            <span class="price-icon">￥</span>
            <span class="price-int">{{priceInt}}</span>
            <span class="price-float">.{{priceFloat}}</span>
          </div>
        </div>
        <div class="qr-wrapper">
          <img :src="canvasSrc" alt="" class="qr">
          <div class="qr-desc">
            <div class="line"><b>①</b> 保存图片到相册</div>
            <div class="line"><b>②</b> 微信扫一扫浏览</div>
          </div>
          <img :src="storeLogo" alt="" class="logo">
        </div>
        <div class="share-name-line">
          <div v-if="isPartner" class="share-name">由{{$store.state.userInfo.username}}分享</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { qrcanvas } from 'qrcanvas'
// import config from '@/config'
import { getProductBasicInfo } from '@/http/service/goods'
import { myManageinfo } from '@/http/service/mine'
import utils from '@/utils/utils'
export default {
  components: {},
  props: {},
  data () {
    return {
      actualPrice: 0,
      pictureList: [],
      isPartner: false,
      mainProductName: '',
      canvasSrc: '',
      selectPictureList: [],
      topPictureUrl: '',
      otherPictureUrlList: [],
      storeLogo: ''
    }
  },
  created () {
    this.storeLogo = `${this.$cdnImageUrl}/${this.$util.isSyBird() ? 'new_sybird_logo' : 'zhijia'}.png`
    this.getIdentify()
    const shareMessage = this.$util.strJSONParse(localStorage.getItem('shareMessage'))
    if (shareMessage) {
      this.createQRCode(shareMessage.mdCode || '')
      this.actualPrice = shareMessage.actualPrice
      if (shareMessage.spuList.length) {
        this.mainProductName = shareMessage.spuList[0].productFullName
        this.getProductInfo(shareMessage)
      }
    }
  },
  mounted () {},
  beforeDestroy () {},
  methods: {
    shareLinkFactory(mdCode) {
      let url = `${location.origin}${location.pathname}`
      const query = this.getCleanQuerys()
      const mid = localStorage.getItem('mId')
      if (mid) {
        query.storeId = mid
        query.shareId = mid
      }
      if (mdCode) {
        query.mdCode = mdCode
      }
      const queryString = utils.parseParamsToQuery(query)
      return url + queryString
    },
    createQRCode (mdCode) {
      // const productId = this.$route.query.productId
      // let url = `${config.HOST}/sgmobile/goodsDetail?productId=${productId}`
      // const mid = localStorage.getItem('mId')
      // if (mid) {
      //   url += `&shareId=${mid}`
      // }
      // if (mdCode) {
      //   url += `&mdCode=${mdCode}`
      // }
      const url = this.shareLinkFactory(mdCode)
      this.$nextTick(() => {
        const canvas1 = qrcanvas({
          data: decodeURIComponent(url), // 分享链接（根据需求来）
          size: 152 // 二维码大小
        })
        this.canvasSrc = canvas1.toDataURL('image/png')
      })
    },
    handleSelectPictureList () {
      this.topPictureUrl = ''
      this.otherPictureUrlList = []
      if (this.selectPictureList.length % 2 === 1) {
        this.topPictureUrl = this.selectPictureList[0]
        if (this.selectPictureList.length > 2) {
          this.otherPictureUrlList = this.selectPictureList.slice(1)
        }
      } else {
        this.otherPictureUrlList = this.selectPictureList
      }
    },
    selectPicture (imgSrc, index) {
      if (index === 0) { return }
      const picIndex = this.selectPictureList.indexOf(imgSrc)
      if (picIndex === -1) {
        if (this.selectPictureList.length < 6) {
          this.selectPictureList.push(imgSrc)
        } else {
          this.$toast('商品规格分类最多选择6个')
        }
      } else {
        this.selectPictureList.splice(picIndex, 1)
      }
    },

    // ajax
    getProductInfo (shareMessage) {
      const promiseList = shareMessage.spuList.map((spu) => {
        return getProductBasicInfo({
          productId: spu.productId,
          channel: '',
          openId: '',
          sceneCode: this.$route.query.fkcode || ''
        })
      })
      Promise.all(promiseList).then((values) => {
        const pictureList = []
        for (let resIndex in values) {
          const res = values[resIndex]
          if (res.success) {
            pictureList.push({ skuShortName: shareMessage.spuList[resIndex].skuShortName, imgSrc: res.data.swiperImgs[0] })
          }
        }
        this.pictureList = pictureList
        this.selectPictureList = [pictureList[0].imgSrc]
      })
    },
    getIdentify () {
      myManageinfo().then((res) => {
        if (res.success) {
          if (res.data.data.identifyFlag && [22, 21, 31].includes(res.data.data.identifyFlag)) {
            this.isPartner = true
          }
        }
      })
    }
  },
  computed: {
    priceInt () {
      return Math.floor(this.actualPrice)
    },
    priceFloat () {
      const float = String(this.actualPrice.toFixed(2)).split('.')[1]
      return float
    }
  },
  watch: {
    selectPictureList: {
      handler () {
        this.handleSelectPictureList()
      },
      immediate: true
    }
  }
}
</script>
